@use '../../styles/colors' as *;
@use '../../styles/fonts' as *;
@use '../../styles/media-breakpoints' as *;

.article-window {
    position: relative;
    background-color: $background-color-editor;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    box-shadow: inset 0px -32px 32px -4px lighten(#0b0e11, 5);
}
.article {    
    margin-bottom: 1em;
    /* Table styling */
    table,
    th,
    td {
        border: 1px solid black;
        border-collapse: collapse;
    }
    th {
        background: $background-color-editor-nav;
    }
}

.article img {
    object-fit: contain;
    max-width: 100%; 
}

.article-text {
    padding-left: 2em;
    padding-right: 2em;
}

.app-main-header {
    display: flex;
    font-size: $font-size-small-paragraph-desktop;
}

.app-main-header-container {
    flex: 1;
    display: flex;
    justify-content: center;
    gap: 0.5em;
    padding: 0 0.5em;
    text-align: center;
}

.app-main-header-text {
    margin: 0;
    font-size: inherit;
}

.app-main-header-button {
    margin-left: auto;
}

.easter-content {
    text-align: center;
}
