@use './fonts.scss' as *;
@use './colors' as *;
@use './media-breakpoints' as *;

body {
    font-family: $font-stack;
    color: $font-color-primary;
}

a:link {
    color: $font-color-links;
}
a:visited {
    color: $font-color-links-visited;
}

p {
    font-size: $font-size-paragraph;
    @media (min-width: $tablet-bp) {
        font-size: $font-size-paragraph-desktop;
    }
}

h1 {
    color: $font-color-secondary;
    font-size: $font-size-h1;
    @media (min-width: $tablet-bp) {
        font-size: $font-size-h1-desktop;
    }
}

h2 {
    color: $font-color-secondary;
    font-size: $font-size-h2;
    @media (min-width: $tablet-bp) {
        font-size: $font-size-h2-desktop;
    }
}

li {
    font-size: $font-size-li;
    @media (min-width: $tablet-bp) {
        font-size: $font-size-li-desktop;
    }
}

/* Zero-Width Space for better accessibility */
// https://developer.mozilla.org/en-US/docs/Web/CSS/list-style-type#accessibility_concerns
li::before {
    content: '\200B';
}

.simplebar-scrollbar::before {
    background-color: $background-color-editor-nav-hover;
}

.unstyled-button {
        /* Reset CSS */
        background: none;
        color: inherit;
        border: none;
        padding: 0;
        font: inherit;
        cursor: pointer;
        outline: inherit;
}

.margin-left-auto {
    margin-left: auto;
}

.margin-right-auto {
    margin-right: auto;
}